import uuidv4 from 'uuid/v4';

import {
	ADD_EXPENSE,
	EDIT_EXPENSE,
	REMOVE_EXPENSE,
} from '../reducers/expenses';

export const addExpense = ({
	description = '',
	note = '',
	amount = 0,
	createdAt = 0,
} = {}) => ({
	type: ADD_EXPENSE,
	expense: { id: uuidv4(), description, note, amount, createdAt },
});

export const removeExpense = ({ id } = {}) => ({
	type: REMOVE_EXPENSE,
	id,
});

export const editExpense = (id, update) => ({
	type: EDIT_EXPENSE,
	id,
	update,
});
